/* =============================================================================
   Article
   ========================================================================== */

article {
    position: relative;
    background: @article-background-color;

    + article {
        margin-top: 2rem;
    }

    header {
        background: @article-meta-background-color;
        margin-bottom: 1rem;

        .entry-title {
            line-height: 1.2em;
            margin: 0;
            color: @font-color-base;

            a {
                color: @font-color-base;

                &:hover,
                &:focus {
                    color: @link-hover-color;
                }
            }

        }

        .entry-meta {
            .small;
            color: #666;
            font-weight: 300;

            > * + * {
                display: inline;
                margin: 0;
                padding-left: .75rem;
            }

            .fa {
                padding-right: .2em;
            }

        }

    }

    .entry-content {

        img {
            max-width: 100%;
            height: auto;
        }

        iframe, object {
            max-width: 100%;
        }

    }

    .screen-sm-max({

        header {

            .entry-title {
                padding: .8rem 1rem 0 1rem;
            }

            .entry-meta {
                padding: .4rem 1rem;
            }

        }

        .entry-content {
            padding: 0 1rem 1rem 1rem;
        }

    });

    .screen-md-min({
        border-radius: @border-radius-medium;

        header {
            border-radius: @border-radius-medium @border-radius-medium 0 0;

            .entry-title {
                padding: .9rem 1.5rem 0 1.5rem;
            }

            .entry-meta {
                padding: .4rem 1.5rem;
            }

        }

        .entry-content {
            padding: 0 1.5rem 1.5rem 1.5rem;
        }

    });

}
